:root {
    --first-color: #1A73E8;
    --input-color: #80868B;
    --border-color: #DADCE8;
    --body-font: 'Roboto', sans-serif;
    --normal-font-size: 1.2rem;
    --small-font-size: .75rem;
}

.header {

    height: 3rem;
    width: 100%;
    /* Full width */
    background-color: white;
    display: flex;
    justify-content: space-between;
    font-size: larger;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.398);
    z-index: 1000;
    /* Ensure it's above other content */
    position: fixed;
    /* Fixed position */
    top: 0;
    left: 0;
    right: 0;
    margin-bottom: 2rem;
    /* Stick to the top of the viewport */

}

.nav__list {
    width: 100%;
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: flex-end;

    height: 100%;


}

.nav__item {
    display: flex;
    align-items: center;
    margin: 0 1.2rem;
}

.nav__link {
    color: black;
    text-decoration: none;

}

.logo-container {
    padding: 0;

}

.nav__item:hover .nav__link {
    color: rgb(4, 155, 4);
}

.logo-container__link {
    height: 3rem;
    padding: 0;
    display: inline-block;

}

.logo-container__link img {
    height: 100%;
}

.nav__list--touch-screen {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    row-gap: 2rem;
    background-color: #fff;
    z-index: 999;
    transition: .25s;
    box-sizing: content-box;
}

.moveTop {
    transition: 0.25s;
    top: -100vh;

}

.nav__menu--oepn-button {
    background-color: transparent;
    border: none;
    outline: none;

    height: 100%;
    margin-right: 1rem;
    cursor: pointer;

}

.nav__menu--oepn-button img {
    height: 90%;
}

.button--close-nav {
    transition: .4s;
    position: absolute;
    right: 18px;
    top: 11px;
    height: 3rem;
    background: transparent;
    border: none;
    outline: none;
    color: black;
    font-size: 1.9rem;
    font-size: 2.5rem;
    cursor: pointer;
}



/* Teacher Login Form component */
.login-form {
    display: flex;
    justify-content: center;
    align-items: center;
    /* height: calc(90vh - 3rem); */
    margin: 3rem 0;

}

.form {
    background-color: #fff;
    width: 360px;

    padding: 4rem 2rem;
    border-radius: 1rem;
    /* box-shadow: 0 10px 25px rgba(92, 99, 105, .2); */
    box-sizing: content-box;
    box-shadow: 0px 5px 10px rgba(99, 98, 100, 0.515);
    margin-top: 2rem;
}

.form__title {
    font-weight: 400;
    margin-bottom: 3rem;
}

.form__div {
    position: relative;
    height: 48px;
    z-index: 2;
    margin-bottom: 1.5rem;


}

.form__input {
    z-index: 1;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;

    font-size: var(--normal-font-size);
    border: none;
    border-bottom: 2px solid var(--border-color);
    padding: 1rem;
    padding-bottom: .1rem;
    outline: none;
    background: none;


    /* border: 1px solid var(--border-color); */
    /* border-radius: .5rem; */
}


.form__label {

    position: absolute;
    left: 1rem;
    top: 1rem;
    padding: 0 .25rem;
    background-color: #fff;
    color: var(--input-color);
    font-size: 1rem;
    z-index: 2;
    transition: .3s;
}

.form__button {
    padding: .75rem 2rem;
    outline: none;
    border: none;

    background-color: var(--first-color);
    color: #fff;

    font-size: var(--normal-font-size);

    border-radius: .5rem;
    cursor: pointer;
    transition: .3s;

}

.form__button:hover {
    box-shadow: 0 10px 36px rgba(0, 0, 0, 0.25);
}

.form-btn-container {
    column-gap: 1rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    justify-content: center;
    margin-top: 3.3rem;
}

.form__input:focus+.form__label {
    top: -.5rem;
    left: 0;
    color: var(--first-color);
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
}

.form__button:last-child {
    background-color: rgb(113, 113, 113);
}

.form__input:not(:placeholder-shown).form__input:not(:focus)+.form__label {
    top: -.5rem;
    left: 0;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;

}

.form__input:focus {
    border: none;
    border-bottom: 2px solid var(--first-color);
    background: none;

}

@media only screen and (max-width:778px) {

    .nav--computer {
        display: none;
        box-sizing: content-box;
        z-index: 999;
    }

    .form {
        box-sizing: border-box;
        width: 96vw;

        box-shadow: 0px 5px 15px rgba(159, 159, 159, 0.607);
    }
}

@media only screen and (min-width:779px) {
    .nav--touch-screen {

        display: none;
    }

    .nav--touch-screen:focus+.form__input {
        /* display: none; */
        background: red;
    }

}


.dashboard {
    display: grid;
    height: 98%;
    width: 99%;
    background: rgba(255, 255, 255, 0.54);
    border-radius: 2rem;
    overflow: hidden;
    grid-template-columns: 11rem auto 20rem;
}

.dental-health__container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;


    margin: 2rem;
    margin-top: 4rem;

}

.dental-health__body {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2rem;

}

section {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    background-color: #98c3fbae;
    border-radius: 20px;
    gap: 3rem;
    padding: 3rem;
    box-sizing: content-box;

}

.dental-health-title {
    font-size: 2rem;
    font-weight: 600;

}

.addGT__btn {
    height: 120px;
    width: 120px;
    padding: 0;
    margin: 1.6rem;
    background-color: #455cf2;
    font-weight: 700;
    border: none;
    font-size: larger;
    border-radius: 10px;
    cursor: pointer;
    transition: .2s;

}

.addGT__btn>.Link {

    width: 100%;
    height: 100%;
    padding: 5px;
    text-align: center;

    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #fff;

}

.addGT__btn:hover {
    background-color: #fff;

    border: 2px solid #455cf2;
}

.addGT__btn:hover .Link {
    width: 100%;
    height: 100%;
    color: #455cf2;
}